import React, { Component } from "react";
import Tip from "./common/tip";
import TipAddPopup from "./common/tipAddPopup";

class Tips extends Component {
  state = {
    tips: [],
    isAddPopupHidden: true,
    APIaddress: this.props.APIaddress,
  };

  componentDidMount = () => {
    this.loadTipsFromDB();
  };

  loadTipsFromDB = () => {
    fetch(`${this.state.APIaddress}/tips/getAll.php`)
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        this.setState({ tips: res });
      });
  };

  handleTipDelete = (tipID) => {
    const tips = this.state.tips.filter((t) => t.id !== tipID);
    fetch(`${this.state.APIaddress}/tips/delete.php?tipID=${tipID}`).then(
      () => {
        this.setState({ tips });
      }
    );
  };

  handleTipEdit = (tipID, content) => {
    this.setState({ isEditPopupHidden: true });

    if (content === "") {
      return;
    }

    fetch(
      `${this.state.APIaddress}/tips/edit.php?tipID=${tipID}&newContent=${content}`
    ).then(() => {
      this.loadTipsFromDB();
    });
  };

  handleTipAdd = (content) => {
    this.setState({ isAddPopupHidden: true });

    if (content === "") {
      return;
    }

    fetch(`${this.state.APIaddress}/tips/add.php?tipContent=${content}`).then(
      () => {
        this.loadTipsFromDB();
      }
    );
  };

  hideTipAddPopup = () => {
    this.setState({ isAddPopupHidden: true });
  };

  showTipAddPopup = () => {
    this.setState({ isAddPopupHidden: false });
  };

  render() {
    return (
      <div>
        <div className="manage-tips flex-column">
          <div className="manage-section-header flex-row">
            <h1 className="manage-section-title">Przydatne porady</h1>
            <div
              className="manage-button add"
              onClick={() => {
                this.showTipAddPopup();
              }}
            >
              Dodaj poradę
            </div>
          </div>
          <div className="tips-content">
            {this.state.tips.map((tip) => (
              <Tip
                key={tip.id}
                id={tip.id}
                content={tip.content}
                onDeleteTip={this.handleTipDelete}
                onEditTip={this.handleTipEdit}
              />
            ))}
          </div>
        </div>

        <TipAddPopup
          addTip={this.handleTipAdd}
          hidden={this.state.isAddPopupHidden}
          hideTipAddPopup={this.hideTipAddPopup}
        />
      </div>
    );
  }
}

export default Tips;
