import React, { Component } from "react";
import Image from "./common/image";
import ImageAddPopup from "./common/imageAddPopup";

class Gallery extends Component {
  state = {
    images: [],
    APIaddress: this.props.APIaddress,
    isImageAddPopupHidden: true,
  };

  componentDidMount = () => this.getImages();

  getImages = () => {
    fetch(`${this.state.APIaddress}gallery/getAll.php`)
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        this.setState({ images: res });
      });
  };

  handleImageDelete = (id) => {
    fetch(`${this.state.APIaddress}/gallery/delete.php?id=${id}`).then(() => {
      const images = this.state.images.filter((e) => e.id !== id);
      this.setState({ images });
      console.log("deleted");
    });
  };

  handleImageVisibilityChange = (id) => {
    const image = this.state.images.find((e) => e.id === id);
    const curImageVisibility = parseInt(image.isVisible);
    const newImageVisibility = curImageVisibility === 1 ? 0 : 1;

    fetch(
      `${this.state.APIaddress}/gallery/changeVisibility.php?id=${id}&isVisible=${newImageVisibility}`
    ).then(() => {
      const images = this.state.images;
      images[images.indexOf(image)].isVisible = newImageVisibility;

      this.setState({ images });
    });
  };

  handleImagesAdd = () => {
    this.getImages();
  };

  hideImageAddPopup = () => {
    this.setState({ isImageAddPopupHidden: true });
  };

  showImageAddPopup = () => {
    this.setState({ isImageAddPopupHidden: false });
  };

  render() {
    return (
      <div className="gallery">
        <div className="manage-section-header flex-row">
          <h1 className="manage-section-title">Galeria</h1>
          <div
            className="manage-button add"
            onClick={() => this.showImageAddPopup()}
          >
            Dodaj zdjęcie
          </div>
        </div>
        <div className="content">
          {this.state.images.map((e) => (
            <Image
              key={e.id}
              id={e.id}
              src={e.name}
              isVisible={e.isVisible}
              onDelete={this.handleImageDelete}
              onVisibilityChange={this.handleImageVisibilityChange}
            />
          ))}
        </div>

        <ImageAddPopup
          hidden={this.state.isImageAddPopupHidden}
          hideImageAddPopup={this.hideImageAddPopup}
          APIaddress={this.state.APIaddress}
          handleImagesAdd={this.handleImagesAdd}
        />
      </div>
    );
  }
}

export default Gallery;
