import React, { Component } from "react";
import TipEditPopup from "./tipEditPopup";

class Tip extends Component {
  state = {
    isEditPopupHidden: true,
  };

  showEditPopup = () => {
    this.setState({ isEditPopupHidden: false });
  };

  hideEditPopup = () => {
    this.setState({ isEditPopupHidden: true });
  };

  render() {
    const { id, content, onDeleteTip, onEditTip } = this.props;

    return (
      <div className="tip">
        <div className="tip-content">{content}</div>
        <div className="flex-row manage-buttons">
          <div className="manage-button delete" onClick={() => onDeleteTip(id)}>
            Usuń
          </div>
          <div
            className="manage-button edit"
            onClick={() => this.showEditPopup()}
          >
            Edytuj
          </div>
        </div>

        <TipEditPopup
          editTip={onEditTip}
          tipID={id}
          tipCurContent={content}
          isHidden={this.state.isEditPopupHidden}
          hideEditPopup={this.hideEditPopup}
        />
      </div>
    );
  }
}

export default Tip;
