import React, { Component } from "react";

class ImageAddPopup extends Component {
  state = {
    filesToUpload: null,
  };

  getClassName = (hidden) => {
    return hidden ? "popup-manage-box hidden" : "popup-manage-box visible";
  };

  handleFilesChange = (event) => {
    this.setState({ filesToUpload: event.target.files });
  };

  handleFileUpload = (files) => {
    const formData = new FormData();

    for (let i = 0; i < files.length; i++) {
      formData.append(`file${i}`, files[i]);
    }

    fetch(`${this.props.APIaddress}/gallery/upload.php`, {
      method: "POST",
      body: formData,
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        this.props.handleImagesAdd();
      });
  };

  render() {
    const { hidden, hideImageAddPopup } = this.props;

    return (
      <div className={this.getClassName(hidden)}>
        <div
          className="close-popup-manage-box"
          onClick={() => hideImageAddPopup()}
        >
          X
        </div>
        <h2 className="popup-manage-box-title">Dodaj zdjęcie</h2>
        <input
          type="file"
          className="file-input"
          multiple
          onChange={this.handleFilesChange}
        />
        <div
          className="submit-button"
          onClick={() => {
            this.handleFileUpload(this.state.filesToUpload);
          }}
        >
          Dodaj
        </div>
      </div>
    );
  }
}

export default ImageAddPopup;
