import React, { Component } from "react";

class TipAddPopup extends Component {
  state = {
    newTipContent: "",
  };

  handleContentChange = (e) => {
    this.setState({ newTipContent: e.target.value });
  };

  getClassName = (hidden) => {
    return hidden ? "popup-manage-box hidden" : "popup-manage-box visible";
  };

  render() {
    const { addTip, hidden, hideTipAddPopup } = this.props;

    return (
      <div className={this.getClassName(hidden)}>
        <div
          className="close-popup-manage-box"
          onClick={() => hideTipAddPopup()}
        >
          X
        </div>
        <h2 className="popup-manage-box-title">Dodaj poradę</h2>
        <textarea
          id="new-tip-content"
          rows="7"
          cols="40"
          onChange={this.handleContentChange}
        ></textarea>
        <div
          className="submit-button"
          onClick={() => addTip(this.state.newTipContent)}
        >
          Dodaj
        </div>
      </div>
    );
  }
}

export default TipAddPopup;
