import React, { Component } from "react";

class TipEditPopup extends Component {
  state = { newTipContent: "" };

  getClassName = (hidden) => {
    return hidden ? "popup-manage-box hidden" : "popup-manage-box visible";
  };

  handleContentChange = (e) => {
    this.setState({ newTipContent: e.target.value });
  };

  render() {
    const {
      editTip,
      isHidden,
      tipID,
      tipCurContent,
      hideEditPopup,
    } = this.props;

    return (
      <div className={this.getClassName(isHidden)}>
        <div className="close-popup-manage-box" onClick={() => hideEditPopup()}>
          X
        </div>
        <h2 className="popup-manage-box-title">Edytuj poradę</h2>
        <textarea
          id="new-tip-content"
          rows="7"
          cols="40"
          defaultValue={tipCurContent}
          onChange={this.handleContentChange}
        ></textarea>
        <div
          className="submit-button"
          onClick={() => {
            editTip(tipID, this.state.newTipContent);
            hideEditPopup();
          }}
        >
          Edytuj
        </div>
      </div>
    );
  }
}

export default TipEditPopup;
