import React, { Component } from "react";
import DeleteImg from "../../img/delete.png";
import VisibilityImg from "../../img/visibility.png";

class Image extends Component {
  getVisibilityClass = (isVisible) => {
    if (isVisible === 0) return "image image-hidden";
    return "image";
  };

  render() {
    const { id, src, isVisible, onDelete, onVisibilityChange } = this.props;

    return (
      <div
        className={this.getVisibilityClass(isVisible)}
        style={{
          backgroundImage: `url("http://localhost/gardening/images/gallery/${src}")`,
        }}
      >
        <div className="image-controls flex-row">
          <img
            src={DeleteImg}
            alt="X"
            className="image-control"
            onClick={() => onDelete(id)}
            title="Usuń"
          />
          <img
            src={VisibilityImg}
            alt="O"
            className="image-control"
            onClick={() => onVisibilityChange(id)}
            title="Pokaż/Ukryj"
          />
        </div>
      </div>
    );
  }
}

export default Image;
